import {api} from '../../../api/api.js'
export default {
	name:"AddressMask",
	props: {
		address:'',
		bz:''
	},
	data() {
		return {
			windowWidth: getApp().globalData.windowWidth,
			windowHeight: getApp().globalData.windowHeight,
			session_id:getApp().globalData.session_id,
			uid:getApp().globalData.uid,
			top: getApp().globalData.top,
			yzm:'',
			ps:''
		}
	},
	methods: {
		addAddress() {
			if(this.yzm === '') {
				uni.showToast({
					title:"请输入验证码",
					icon:"none"
				})
				return;
			}else if(this.ps === '') {
				uni.showToast({
					title:"请输入交易密码",
					icon:"none"
				})
				return;
			}
			uni.request({
				url:api.add_address,
				data: {
					uid:this.uid,
					session_id:this.session_id,
					address:this.address,
					label:this.bz,
					paypassword:this.ps,
					yzm_code:this.yzm
				},
				method:"post",
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: (res) => {
					if(res.data.code === "success") {
						uni.showToast({
							title:res.data.msg,
							duration:1500,
							icon:"none"
						})
						this.$emit("close");
					}else {
						uni.showToast({
							title:res.data.msg,
							duration:1500,
							icon:"none"
						})
					}
				}
			})
		},
		close() {
			this.$emit("close")
		}
	}
}
